@import "../../../styles/helpers/_helpers";

.block-advantages {
	display: grid;
	grid-template-columns: repeat(var(--grid-column), 1fr);
	gap: var(--grid-gap);
	.block__content {
		display: contents;
	}
	.block__info {
		grid-column: 2/span 5;
	}
	.advantages-work {
		grid-column: 7/span 7;
		grid-row: 1/4;
	}
}
